<template>
	<view class="profile-container">
		<view class="login-section" v-if="!isLoggedIn">
			<image class="avatar" src="/static/logo.png" mode="aspectFill"></image>
			<view class="login-text">ËØ∑ÁôªÂΩï</view>
			<button class="login-btn wechat-btn" open-type="getUserInfo" @getuserinfo="onGetUserInfo">
				<image src="/static/common/ic_btn_cn~iphone.png" mode="aspectFit" class="wechat-icon"></image>
				ÂæÆ‰ø°‰∏ÄÈîÆÁôªÂΩï
			</button>
			<view class="login-tips">ÁôªÂΩïÂêéÂèØÂêåÊ≠•Â≠¶‰π†ËÆ∞ÂΩïÂíåÊî∂Ëóè</view>
		</view>
		<view class="user-info" v-else>
			<image class="avatar" :src="userInfo.avatarUrl || '/static/logo.png'" mode="aspectFill"></image>
			<view class="user-name">{{userInfo.nickName || 'Áî®Êà∑Âêç'}}</view>
		</view>
		
		<view class="menu-list">
			<view class="menu-item" @click="handleMenuItem('history')">
				<text class="menu-icon">üìö</text>
				<text class="menu-text">Â≠¶‰π†ËÆ∞ÂΩï</text>
				<text class="menu-arrow">></text>
			</view>
			<view class="menu-item" @click="handleMenuItem('favorites')">
				<text class="menu-icon">‚≠ê</text>
				<text class="menu-text">Êî∂ËóèÂçïËØç</text>
				<text class="menu-arrow">></text>
			</view>
			<view class="menu-item" @click="handleMenuItem('settings')">
				<text class="menu-icon">‚öôÔ∏è</text>
				<text class="menu-text">ËÆæÁΩÆ</text>
				<text class="menu-arrow">></text>
			</view>
			<view class="menu-item" @click="handleMenuItem('about')">
				<text class="menu-icon">‚ÑπÔ∏è</text>
				<text class="menu-text">ÂÖ≥‰∫éÊàë‰ª¨</text>
				<text class="menu-arrow">></text>
			</view>
		</view>
		
		<view class="logout-btn" v-if="isLoggedIn" @click="logout">
			ÈÄÄÂá∫ÁôªÂΩï
		</view>
	</view>
</template>

<script>
import api from '../../utils/api.js';

export default {
	data() {
		return {
			isLoggedIn: false,
			userInfo: {
				nickName: '',
				avatarUrl: ''
			}
		}
	},
	onLoad() {
		// Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁôªÂΩï
		this.checkLoginStatus();
	},
	methods: {
		checkLoginStatus() {
			// ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñtokenÂíåÁî®Êà∑‰ø°ÊÅØ
			const token = uni.getStorageSync('token');
			const userInfo = uni.getStorageSync('userInfo');
			
			// Âè™ÊúâtokenÂíåuserInfoÈÉΩÂ≠òÂú®ÊâçËÆ§‰∏∫ÊòØÂ∑≤ÁôªÂΩïÁä∂ÊÄÅ
			if (token && userInfo) {
				this.isLoggedIn = true;
				this.userInfo = JSON.parse(userInfo);
				
				// ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†tokenÊúâÊïàÊÄßÈ™åËØÅÈÄªËæë
				// ‰æãÂ¶ÇÊ£ÄÊü•tokenÊòØÂê¶ËøáÊúüÔºåÂ¶ÇÊûúËøáÊúüÂàôÊ∏ÖÈô§ÁôªÂΩïÁä∂ÊÄÅ
				
				console.log('Áî®Êà∑Â∑≤ÁôªÂΩïÔºåtoken:', token);
			} else {
				// Â¶ÇÊûútoken‰∏çÂ≠òÂú®‰ΩÜuserInfoÂ≠òÂú®ÔºåÊ∏ÖÈô§userInfo
				if (!token && userInfo) {
					uni.removeStorageSync('userInfo');
				}
				
				this.isLoggedIn = false;
				console.log('Áî®Êà∑Êú™ÁôªÂΩï');
			}
		},
		onGetUserInfo(e) {
			if (e.detail.errMsg === 'getUserInfo:ok') {
				uni.showLoading({
					title: 'ÁôªÂΩï‰∏≠...'
				});
				
				// 1. Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïÂá≠ËØÅcode
				uni.login({
					provider: 'weixin',
					success: (loginRes) => {
						// 2. Ë∞ÉÁî®ÂÆûÈôÖAPIÁôªÂΩïÊé•Âè£
						api.login(loginRes.code, e.detail.userInfo)
							.then(res => {
								if (res.statusCode === 200 && res.data.code === 200) {
									// ‰øùÂ≠òtokenÂíåÁî®Êà∑ID
									uni.setStorageSync('token', res.data.data.token);
									uni.setStorageSync('userId', res.data.data.userId);
									
									// ‰øùÂ≠òÁî®Êà∑Âü∫Êú¨‰ø°ÊÅØ
									const userInfo = {
										nickName: e.detail.userInfo.nickName,
										avatarUrl: e.detail.userInfo.avatarUrl
									};
									uni.setStorageSync('userInfo', JSON.stringify(userInfo));
									
									this.isLoggedIn = true;
									this.userInfo = userInfo;
									
									// Ëé∑ÂèñÊõ¥ËØ¶ÁªÜÁöÑÁî®Êà∑‰ø°ÊÅØ
									this.fetchUserDetails(res.data.data.userId);
									
									uni.hideLoading();
									uni.showToast({
										title: 'ÁôªÂΩïÊàêÂäü',
										icon: 'success'
									});
								} else {
									uni.hideLoading();
									uni.showToast({
										title: res.data.message || 'ÁôªÂΩïÂ§±Ë¥•',
										icon: 'none'
									});
								}
							})
							.catch(err => {
								uni.hideLoading();
								uni.showToast({
									title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
									icon: 'none'
								});
								console.error('APIËØ∑Ê±ÇÂ§±Ë¥•:', err);
							});
					},
					fail: (err) => {
						uni.hideLoading();
						uni.showToast({
							title: 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•',
							icon: 'none'
						});
						console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•:', err);
					}
				});
			} else {
				uni.showToast({
					title: 'ÊéàÊùÉÂ§±Ë¥•',
					icon: 'none'
				});
			}
		},
		
		// Ëé∑ÂèñÁî®Êà∑ËØ¶ÁªÜ‰ø°ÊÅØ
		fetchUserDetails(userId) {
			api.getUserInfo(userId)
				.then(res => {
					if (res.statusCode === 200) {
						// ÂêàÂπ∂Áî®Êà∑‰ø°ÊÅØ
						const userDetails = res.data;
						const userInfo = {
							...this.userInfo,
							level: userDetails.level || 1,
							points: userDetails.points || 0
						};
						
						// Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®ÂíåÁä∂ÊÄÅ
						uni.setStorageSync('userInfo', JSON.stringify(userInfo));
						this.userInfo = userInfo;
					}
				})
				.catch(err => {
					console.error('Ëé∑ÂèñÁî®Êà∑ËØ¶ÊÉÖÂ§±Ë¥•:', err);
				});
		},
		logout() {
			uni.showModal({
				title: 'ÈÄÄÂá∫ÊèêÁ§∫',
				content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
				success: (res) => {
					if (res.confirm) {
						// Ê∏ÖÈô§Áî®Êà∑‰ø°ÊÅØÂíåtoken
						uni.removeStorageSync('userInfo');
						uni.removeStorageSync('token');
						this.isLoggedIn = false;
						this.userInfo = {
							nickName: '',
							avatarUrl: ''
						};
						
						uni.showToast({
							title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
							icon: 'success'
						});
					}
				}
			});
		},
		handleMenuItem(type) {
			// Â¶ÇÊûúÊú™ÁôªÂΩïÔºåÂàôÊèêÁ§∫ÈúÄË¶ÅÁôªÂΩï
			if (!this.isLoggedIn) {
				uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Ê≠§ÂäüËÉΩÈúÄË¶ÅÁôªÂΩïÂêéÊâçËÉΩ‰ΩøÁî®',
					confirmText: 'ÂéªÁôªÂΩï',
					success: (res) => {
						// Áî®Êà∑ÁÇπÂáªÁ°ÆÂÆöÔºå‰∏çÈúÄË¶ÅÂÅöÈ¢ùÂ§ñÊìç‰ΩúÔºåÂõ†‰∏∫ÁïåÈù¢‰∏äÂ∑≤ÁªèÊúâÁôªÂΩïÊåâÈíÆ
					}
				});
				return;
			}
			
			// Ê†πÊçÆ‰∏çÂêåÁöÑËèúÂçïÈ°πÊâßË°å‰∏çÂêåÁöÑÊìç‰Ωú
			switch(type) {
				case 'history':
					uni.showToast({
						title: 'Â≠¶‰π†ËÆ∞ÂΩïÂäüËÉΩÂºÄÂèë‰∏≠',
						icon: 'none'
					});
					break;
				case 'favorites':
					uni.showToast({
						title: 'Êî∂ËóèÂçïËØçÂäüËÉΩÂºÄÂèë‰∏≠',
						icon: 'none'
					});
					break;
				case 'settings':
					uni.showToast({
						title: 'ËÆæÁΩÆÂäüËÉΩÂºÄÂèë‰∏≠',
						icon: 'none'
					});
					break;
				case 'about':
					uni.showToast({
						title: 'ÂÖ≥‰∫éÊàë‰ª¨ÂäüËÉΩÂºÄÂèë‰∏≠',
						icon: 'none'
					});
					break;
			}
		}
	}
}
</script>

